header h3{
    display: inline-block;
    width:33%;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
}

header{
    background-image: -webkit-linear-gradient(#003cff, #00a2ec, #003cff);
    background-image: linear-gradient(#003cff, #00a2ec, #003cff);
    color: white;
    text-shadow: 2px 2px #000000;
    width: 100%;
    padding: 1%;
    position: fixed;
    display: block;
    box-shadow: 0 3px 6px #000000, 0 3px 6px #000000;
}


.desc-correct {
    -webkit-animation: toGreen 0.3s;
            animation: toGreen 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    color: #0f0;
}

.desc-incorrect {
    -webkit-animation: toRed 0.3s;
            animation: toRed 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    color: #f00;
}


@-webkit-keyframes toGreen{
    from {color: #fff;}
    to {color: #0f0;}
    /* from {color: #0f0;}
    to {color: #fff;} */
}


@keyframes toGreen{
    from {color: #fff;}
    to {color: #0f0;}
    /* from {color: #0f0;}
    to {color: #fff;} */
}

@-webkit-keyframes toRed{
    from {color: #0f0;}
    to {color: #f00;}
    /* from {color: #f00;}
    to {color: #fff;} */
}

@keyframes toRed{
    from {color: #0f0;}
    to {color: #f00;}
    /* from {color: #f00;}
    to {color: #fff;} */
}



#banner{
    text-align: center;
    display:block;
    color: white;
    text-shadow: 2px 2px #000000;
    background-image: url(/click-game/static/media/mmxp2.1e41d16b.png);
    background-repeat: no-repeat;
    background-size: 100%;
    padding: 10%;
    border-color: black;
    border-width: 0px 0px 1px 0px;
    border-style: solid;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}
footer{
    background-image: -webkit-linear-gradient(#003cff, #00a2ec, #003cff);
    background-image: linear-gradient(#003cff, #00a2ec, #003cff);
    width: 100%;
    color: white;    
    text-shadow: 2px 2px #000000;
    box-shadow: 0 3px 6px #000000, 0 3px 6px #000000;
}
* { padding: 0; margin: 0; }
.clickItem {
    background: #fff;
    border-radius: 2px;
    overflow: hidden;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}
.clickItem > .img-container {
    width: 100%;
    height: auto;
    overflow: hidden;
    text-align: center;
}
.clickItem > .img-container img {
    margin-top: 10px;
    width: 100%;
    height: 15rem;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    #clickHolder{
        width: 50%;
        margin-left: 25%;
        margin-top: 2.5%
    }
    .clickItem {
        margin: 5%;
        display: inline-block;
        border-radius: 2px;
        box-shadow: 0 3px 6px #999, 0 3px 6px #999; 
        overflow: hidden;
        width: 100%;
    }
} 
/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
    #clickHolder{
        width: 60%;
        margin-left: 20%;
        margin-top: 2.5%
    }
    .clickItem {
        width: 45%;
        margin: 2.5%;
        display: inline-block;
    }
} 

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    #clickHolder{
        width: 70%;
        margin-left: 15%;
        margin-top: 2.5%
    }
    .clickItem {
        width: 30%;
        margin: 1.5%;
        display: inline-block;
    }
} 

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    #clickHolder{
        width: 70%;
        margin-left: 15%;
    }
    .clickItem {
        width: 22.5%;
        margin: 1.25%;
        display: inline-block;
    }
} 

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
    #clickHolder{
        width: 60%;
        margin-left: 20%;
    }
    .clickItem {
        width: 22%;
        margin: 1.5%;
        display: inline-block;
    }
}

.style_prevu_kit_ex {
    -webkit-animation: shake 0.3s;
            animation: shake 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}

@keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}

